'use client'
import { useEffect, useState } from 'react'
import { getSheetData } from '../lib/fetchGoogleSheet'
import Link from 'next/link'

export default function ProjectsGrid() {
  const [projects, setProjects] = useState<any[]>([])

  useEffect(() => {
    async function fetchProjects() {
      const data = await getSheetData('Projects')
      setProjects(data)
    }
    fetchProjects()
  }, [])

  return (
    <section className="py-16 bg-white">
      <div className="container mx-auto px-4">
        <h2 className="text-4xl font-bold mb-10 text-center">Our Projects</h2>
        <div className="grid md:grid-cols-3 gap-6">
          {projects.map((p) => (
            <Link
              key={p.Slug}
              href={`/projects/${p.Slug}`}
              className="rounded-lg shadow hover:shadow-lg overflow-hidden bg-white"
            >
              <img src={`/img/${p.Image}`} alt={p.Title} className="h-56 w-full object-cover" />
              <div className="p-4">
                <div className="text-sm text-blue-600 mb-1">{p.Category}</div>
                <h3 className="text-lg font-semibold">{p.Title}</h3>
                <p className="text-slate-600 text-sm mt-2">{p.ShortDescription}</p>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </section>
  )
}
